"use client";

// React & Next Imports
import { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";
import Link from "next/link";

//3rd Party
import {
  Popover,
  PopoverButton,
  PopoverPanel,
  Transition,
} from "@headlessui/react";

//Component

//Api
import { DefaultAddressApi } from "@/api/clientsideaction/profileapi";

export default function DeliveryCart({
  countryOption,
  profilename,
  handleDelete,
  profiledAddress,
  handleAddress,
}) {
  const { t } = useTranslation();
  const [editRadio, setEditRadio] = useState();
  
  const handleChangeDefaultAddress = (index, id) => {
    setEditRadio(index);
    DefaultAddressApi(id, 0, handleAddress);
  };
  const handleCountry = (ids) => {
    const filteredPeople = countryOption?.filter(
      (data) => data?.countryId == ids
    );
    if (filteredPeople) {
      return filteredPeople?.[0]?.name;
    }
  };

  return (
    <div className="relative p-4 mb-6 border rounded border-grey">
      <div className="flex justify-between flex-col min-[430px]:flex-row">
        <h3 className="text-base font-black text-black-300">
          {t("myprofile.DelivAddr")}
        </h3>
        <Link
          prefetch={false}
          href={{
            pathname: "/account-settings/my-profile",
            query: { deliveryaddress: "add" },
          }}
          className="relative flex items-center h-6 font-medium text-link text-sm style-2 !pl-0"
        >
          {t("myprofile.AddDelivAddr")}
        </Link>
      </div>
      {profiledAddress &&
        profiledAddress?.map(
          (data, index) =>
            data?.addressType == 0 && (
              <div
                key={index}
                className={`p-4 flex justify-between items-start ${
                  editRadio == index && "bg-blue-100"
                }`}
              >
                <div className="flex space-x-4 rtl:space-x-[unset]">
                  <input
                    id="comments"
                    name="comments"
                    type="radio"
                    onChange={() =>
                      handleChangeDefaultAddress(index, data?.addressId)
                    }
                    checked={data?.isDefault == 1}
                    className="text-black rounded-full cursor-pointer rtl:ml-4 focus:ring-0 focus:ring-transparent w-20px h-20px radio-black"
                  />
                  <div>
                    {profilename?.firstName && (
                      <h4 className="mb-2.5 font-bold text-black-300">
                        {profilename?.firstName}
                      </h4>
                    )}
                    <p className="mb-2.5 font-normal text-black-300 text-sm">
                      {data?.address1}, {data?.address2}, {data?.city},{" "}
                      {data?.zone?.name}, {handleCountry(data?.countryId)} -
                      {data?.postcode}
                    </p>
                    {data?.phoneNo && (
                      <p className="text-sm font-normal text-black-300">
                        {t("myprofile.Mobile")} : {data?.phoneNo}
                      </p>
                    )}
                  </div>
                </div>
                <div>
                  <Popover className="relative">
                    <PopoverButton className="flex items-center focus-visible:outline-none min-w-1">
                      <img src="/img/option.svg" alt="option" />
                    </PopoverButton>

                    <Transition
                      as={Fragment}
                      enter="transition ease-out duration-200"
                      enterFrom="opacity-0 translate-y-1"
                      enterTo="opacity-100 translate-y-0"
                      leave="transition ease-in duration-150"
                      leaveFrom="opacity-100 translate-y-0"
                      leaveTo="opacity-0 translate-y-1"
                    >
                      <PopoverPanel className="top-full right-0 rtl:right-[unset] rtl:left-0 z-10 absolute bg-white shadow-lg mt-3 p-1.5 rounded ring-1 ring-gray-900/5 w-screen max-w-28 overflow-hidden">
                        <div className="group relative flex items-center space-x-2 rtl:space-x-[unset] hover:bg-[#e8e8e8] p-2 rounded-lg text-sm leading-6">
                          <div className="flex items-center justify-center flex-none w-4 h-4 rounded-lg rtl:ml-2">
                            <img src="/img/edit-black.svg" alt="edit-black" />
                          </div>
                          <div className="flex-auto">
                            <Link
                              prefetch={false}
                              href={{
                                pathname: "/account-settings/my-profile",
                                query: { deliveryaddress: data?.addressId },
                              }}
                              className="block text-sm font-normal text-black-300"
                            >
                              {t("myprofile.Edit")}
                            </Link>
                          </div>
                        </div>
                        <div className="group relative flex items-center space-x-2 rtl:space-x-[unset] hover:bg-[#e8e8e8] p-2 rounded-lg text-sm leading-6">
                          <div className="flex items-center justify-center flex-none w-4 h-4 rounded-lg rtl:ml-2">
                            <img src="/img/delete.svg" alt="delete" />
                          </div>
                          <div className="flex-auto">
                            <a
                              role="button"
                              className="block text-sm font-normal cursor-pointer text-black-300"
                              onClick={() => handleDelete(data?.addressId)}
                            >
                              {t("myprofile.Delete")}
                            </a>
                          </div>
                        </div>
                      </PopoverPanel>
                    </Transition>
                  </Popover>
                </div>
              </div>
            )
        )}
    </div>
  );
}
