"use client";

//React & Next Imports
import { useEffect } from "react";
import { useTranslation } from "react-i18next";

//3rd Party
import OTPInput from "react-otp-input";

//Component

//Api
import {
  MyProfileEmailOtpverify,
  MyProfileEmailVerify,
} from "@/api/clientsideaction/profileapi";

export default function ModelEmailVarify({
  emailmodel,
  setEmailModel,
  email,
  password,
  otp,
  setOtp,
  timer,
  isTimerRunning,
  minutes,
  zero,
  setIsTimerRunning,
  setMinutes,
  setTimer,
  setOtpStatus,
  setEmailStatus,
}) {
  const { t } = useTranslation();
  
  useEffect(() => {
    let myInterval = setInterval(() => {
      if (timer > 0) {
        setTimer(timer - 1);
      }
      if (timer === 0) {
        if (minutes === 0) {
          setIsTimerRunning(false);
          clearInterval(myInterval);
        } else {
          setMinutes(minutes - 1);
          setTimer(59);
        }
      }
    }, 1000);
    return () => {
      clearInterval(myInterval);
    };
  }, [timer, isTimerRunning, minutes]);

  const handleVerifyOtp = () => {
    if (otp.length == 6 && isTimerRunning) {
      MyProfileEmailOtpverify(
        email,
        otp,
        setMinutes,
        setTimer,
        setIsTimerRunning,
        setEmailModel,
        setOtpStatus
      );
    }
  };
  useEffect(() => {
    if (emailmodel) {
      document.body.classList.add("overflow-hidden");
    } else {
      document.body.classList.remove("overflow-hidden");
    }
  }, [emailmodel]);
  const handleCloseModel = () => {
    setEmailModel(false);
    setOtpStatus(false);
    setEmailStatus(false);
    setOtp();
  };
  const handleResetOtp = () => {
    setOtp("");
    MyProfileEmailVerify(
      email,
      setMinutes,
      setTimer,
      setIsTimerRunning,
      setEmailModel,
      setEmailStatus,
      password
    );
  };
  const handleEnterOTPVarify = (e) => {
    if (e.key === "Enter") {
      handleVerifyOtp();
    }
  };
  return (
    <div
      aria-modal="true"
      id="static-modal"
      data-modal-backdrop="static"
      tabIndex="-1"
      aria-hidden="true"
      className={`${
        emailmodel
          ? "fixed m-0 left-0 right-0 top-0 z-50  max-h-full w-full overflow-y-auto overflow-x-hidden p-4 md:inset-0 justify-center items-center flex bg-light-modal"
          : "hidden"
      }`}
    >
      <div className="relative w-full max-w-md max-h-full">
        {/* <!-- Modal content --> */}
        <div className="relative bg-white shadow dark:bg-gray-700">
          <div className="absolute top-6 right-6">
            <button data-modal-hide="static-modal" onClick={handleCloseModel}>
              <img src="/img/modal-x.svg" alt="modal-x" />
            </button>
          </div>
          {/* <!-- Modal body --> */}
          <div className="p-8 pb-0">
            <h3 className="mb-3 text-base font-bold text-black-300">
              {t("myprofile.EntOTP")}
            </h3>
            <p className="mb-4 text-sm font-normal text-black-300">
              {t("myprofile.OTPsent")} {email} {t("myprofile.ForVerification")}.
            </p>
            <div className="flex justify-center items-center text-black gap-6 p-3.5 border border-grey-400 rounded mb-8 otp-email">
              <OTPInput
                inputType="number"
                value={otp}
                onChange={setOtp}
                placeholder={zero}
                numInputs={6}
                renderInput={(props) => (
                  <input
                    {...props}
                    onKeyDown={(e) => handleEnterOTPVarify(e)}
                  />
                )}
              />
            </div>
            <div className="flex items-end justify-between mb-8">
              <div>
                <h6 className="mb-1 text-xs font-normal text-black-300">
                  {t("myprofile.Didotp")}
                </h6>
                <p className="text-sm font-bold text-black-300">
                  {t("signin.ResendOTP")} in {minutes}: {timer}
                </p>
              </div>
              <button
                disabled={isTimerRunning}
                className={`font-bold text-sm ${
                  isTimerRunning ? "text-light-100" : "text-black"
                }`}
                onClick={handleResetOtp}
              >
                {t("signin.ResendOTP")}
              </button>
            </div>
          </div>
          {/* <!-- Modal footer --> */}
          <div className="flex items-center justify-end px-8 py-4 space-x-2 text-white border-t border-gray-200 rounded-b rtl:space-x-reverse dark:border-gray-600">
            <button
              type="button"
              disabled={otp?.length !== 6 ? true : false}
              className="px-8 py-4 text-sm font-semibold text-white border border-gray-200 rounded bg-boldblack focus:z-10 focus:outline-none focus:ring-4 focus:ring-blue-300 dark:border-gray-500 dark:bg-gray-700 dark:text-gray-300 dark:hover:bg-gray-600 dark:hover:text-white"
              onClick={handleVerifyOtp}
            >
              {t("myprofile.VerifyOTP")}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
