"use client";

//React and Next
import { useEffect, useState } from "react";

//API
import { MyprofileAddressDelete } from "@/api/clientsideaction/profileapi";
import {
  CustomerProfileAddressApi,
  CustomerProfileDataApi,
} from "@/api/clientsideaction/myProfile";

//Components
import Sidebar from "./sidebar";
import Profilebar from "./profile-content";
import Contactus from "../Contactus";
import ProfileEdit from "./ProfileEdit";
import AddDeliveryAddress from "./AddDeliveryAddress";
import AddBillingAddress from "./AddBillingAddress";
import Breadcrumb from "../Breadcrumbs";
import { useTranslation } from "react-i18next";

export default function Myprofile({
  token,
  searchParams,
  verify,
  profiledatacountry,
  profilestate,
}) {
  const [triger, setTriger] = useState(0);
  const [profileEditData, setProfileEditData] = useState();
  const [addressData, setAddressData] = useState();
  const { t } = useTranslation();
  useEffect(() => {
    if (triger !== 0) {
      CustomerProfileDataApi(setProfileEditData);
      CustomerProfileAddressApi(setAddressData);
    }
  }, [triger]);
  const handleAddress = () => {
    CustomerProfileAddressApi(setAddressData);
  };
  useEffect(() => {
    CustomerProfileDataApi(setProfileEditData);
    CustomerProfileAddressApi(setAddressData);
  }, []);

  const handleDelete = (id) => {
    MyprofileAddressDelete(id, triger, setTriger, handleAddress);
  };

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "javascript:void(0)",
    },
  ];

  const profileBreadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "/account-settings/my-profile",
    },
    {
      text: t("Breadcrumb.profileEdit"),
      url: "javascript:void(0)",
    },
  ];

  const DeliveryBreadCrumbAdd = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "/account-settings/my-profile",
    },
    {
      text: t("Breadcrumb.addDeliveryAddress"),
      url: "javascript:void(0)",
    },
  ];

  const DeliveryBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "/account-settings/my-profile",
    },
    {
      text: t("Breadcrumb.editDeliveryAddress"),
      url: "javascript:void(0)",
    },
  ];

  const billingBreadCrumbAdd = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "/account-settings/my-profile",
    },
    {
      text: t("Breadcrumb.addBillingAddress"),
      url: "javascript:void(0)",
    },
  ];

  const billingBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.myProfile"),
      url: "/account-settings/my-profile",
    },
    {
      text: t("Breadcrumb.editBillingAddress"),
      url: "javascript:void(0)",
    },
  ];

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb
          breadCrumb={
            searchParams?.profile
              ? profileBreadCrumb
              : searchParams?.deliveryaddress == "add"
              ? DeliveryBreadCrumbAdd
              : Number(searchParams?.deliveryaddress)
              ? DeliveryBreadCrumbEdit
              : searchParams?.billingaddress == "add"
              ? billingBreadCrumbAdd
              : Number(searchParams?.billingaddress)
              ? billingBreadCrumbEdit
              : breadCrumb
          }
        />

        <div className="flex md:space-x-8 rtl:space-x-[unset] mx-auto mt-4 mb-7">
          <Sidebar />
          {searchParams?.profile ? (
            <ProfileEdit
              profiledata={profileEditData}
              verify={verify}
              triger={triger}
              setTriger={setTriger}
              profilestate={profilestate?.data}
              profiledatacountry={profiledatacountry?.data}
            />
          ) : searchParams?.deliveryaddress ? (
            <AddDeliveryAddress
              profiledata={profileEditData}
              triger={triger}
              setTriger={setTriger}
              searchParams={searchParams}
              profilestate={profilestate?.data}
              profiledatacountry={profiledatacountry?.data}
            />
          ) : searchParams?.billingaddress ? (
            <AddBillingAddress
              profiledata={profileEditData}
              triger={triger}
              setTriger={setTriger}
              searchParams={searchParams}
              profilestate={profilestate?.data}
              profiledatacountry={profiledatacountry?.data}
            />
          ) : (
            <Profilebar
              addressData={addressData}
              profiledata={profileEditData}
              profiledatacountry={profiledatacountry}
              handleDelete={handleDelete}
              handleAddress={handleAddress}
            />
          )}
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
